import React, { Component } from "react"
import { graphql } from "gatsby"
import styled from "@emotion/styled"
import posed from "react-pose"
import { ReactComponent as ChainIcon } from "../images/chain.svg"
import { DOMParser } from "xmldom"
import {
  PostHeaderTitle,
  Header,
  HeaderIcon,
  SideBarLink,
  RightSidebar,
  SideBarTitle,
  IndentedContent,
  SidebarLinkWithIcon,
} from "../components/style"
import { ReactComponent as PostIcon } from "../images/chain.svg"
import { MobileView } from "react-device-detect"
import burgerMenu from "../images/hamburger_menu.svg"
import color from "../components/color"
//import SmoothScroll from "smooth-scroll"

const SideBarContent = styled.div`
  padding: 5px 0;  
  ul {
    list-style: none;
    text-align: left;
    margin: 0 auto;
    padding-left: 3px;
  }
  li {
    a {
      border: 0;
    }
  }
`

const SideBarWrapper = styled.div`
  width: 250px;
  margin-top: 10px;
  flex-wrap: wrap;
  display: inline-flex;
`

const PostContainer = styled.div`
  width: 100vw;
  display: inline-block;
  text-align: left;

  @media only screen and (min-width: 768px) {
    width: 745px;
  }
  
  @media only screen and (min-width: 1400px) {
    width: 1000px;
  }
`

const RightToLeft = posed(PostContainer)({
  // enter: {
  //   x: 0,
  //   opacity: 1,
  //   transition: { ease: "easeOut", duration: 600 },
  // },
  // exit: {
  //   x: 400,
  //   opacity: 0,
  //   transition: { ease: "easeOut", duration: 600 },
  // },
})

const Anchor = styled.div`
  width:0;
  height:0;
`

const TopAnchor = styled(Anchor)`
  position: absolute;
  top: -30px;
`

const PostFrame = styled.div`
  position: relative;
  min-height: 300px;
`

const PostContent = styled.div`
  margin: 15px;
  display: inline-block;
  p {
   padding: 0 10px;
   margin: 10px 0;
  }
  ol {
    margin: 10px 0;
  }
`

const CommentoWrapper = styled.div`
  margin: 15px;
`
const Post = styled.div`
  display: inline-block;
   width: 100vw;
   
  @media only screen and (min-width: 768px) {
    width: 650px;
  }
  
  @media only screen and (min-width: 1400px) {
    width: 900px;
  }
  

`

class Commento extends React.Component {
  constructor(...args) {
    super(...args)
    this.ref = React.createRef()
  }

  render() {
    return (
      <CommentoWrapper>
        <div id="commento"></div>
        <div ref={this.ref}/>
      </CommentoWrapper>
    )
  }

  componentDidMount() {
    let async = true
    const s = document.createElement("script")
    s.async = async
    s.src = "https://cdn.commento.io/js/commento.js"
    // s.setAttribute('data-timestamp', +new Date())
    this.ref.current.appendChild(s)
  }
}

const PostWrapper = styled.div`
  width: 100vw;
  margin: 0 auto;
  
  @media only screen and (min-width: 768px) { 
     width: 1000px;
  }
  
  @media only screen and (min-width: 1400px) {
      width: 1280px;
  }
  

`

const ContentName = styled.span`
  vertical-align: middle;
`

const ContentNumber = styled.span`
  line-height: 28px;
  &:after{
    content:". ";
  }
`

const PullTab = styled.div`
  width: 32px;
  height: 32px;
  position: fixed;
  left: 10px;
  top: 50px;
  z-index: 4;
  background-image: url(${burgerMenu});
  background-size: cover;
`

const PostContentHeader = styled.div`
  color: ${color.date_info}
`

class BlogPost extends Component {
  // constructor to set state and bind "this"

  state = {
    isExpanded: false,
    isSearchMode: false
  };

  //
  // constructor(props) {
  //   super(props);
  // }

  // function to handle the click
  expandClick = () => {
    this.setState(state => ({ isExpanded: !state.isExpanded }));
  };

  foldClick = () => {
    if (this.state.isExpanded) {
      this.setState({ isExpanded: false });
    }
  };


  expandSearch = () =>{
    this.setState(state => ({ isSearchMode: !state.isSearchMode }));
  };

  preventClick = (e,currentPath) => {
    if(currentPath===1) {
      e.preventDefault();
      e.stopPropagation();
      e.nativeEvent.stopImmediatePropagation();
    }
  };



  render() {
    const post = this.props.data.markdownRemark;
    //let scroll = new SmoothScroll('a[href*="#"]');
    // the return() to put your default HTML into the DOM
    return (
      <>
      <PostWrapper>
        <PostFrame onClick={this.foldClick} >
          <TopAnchor id="top-anchor"/>
          <RightSidebar active={this.state.isExpanded ? 1:0}>
            <SideBarWrapper>
              <SideBarTitle>
                TABLE OF CONTENT
              </SideBarTitle>
              <IndentedContent>
                {(() => {
                  if (post.tableOfContents !== undefined && post.tableOfContents !== "") {
                    const toc = new DOMParser().parseFromString(post.tableOfContents, "text/html")
                    if (toc !== undefined) {
                      const list = toc.getElementsByTagName("a")
                      let allData = []
                      let iterator = 0
                      for (let listData of Array.from(list)) {
                        let element = Array.from(listData.childNodes).map(el => el.nodeValue)
                        if (element.length > 0) {
                          iterator++
                          let currentLi = React.createElement("li", { "key": "li_" + element[0] }, [
                              React.createElement(SidebarLinkWithIcon, {
                                  "key": "lwi_" + iterator,
                                  "to": listData.getAttribute("href"),
                                  "active": 0,
                                },
                                React.createElement(SideBarLink, {}, [
                                  React.createElement(ChainIcon, { "key": "ci_" + iterator }),
                                  React.createElement(ContentNumber, { "key": "cnum_" + iterator }, iterator.toString().padStart(2, "0")),
                                  React.createElement(ContentName, { "key": "cnam_" + iterator }, element[0]),
                                ]),
                              ),
                            ],
                          )
                          allData.push(currentLi)
                        }
                      }
                      return React.createElement(SideBarContent, {},
                        React.createElement("ul", {}, allData),
                      )
                    }
                  }
                })()}
              </IndentedContent>
            </SideBarWrapper>
          </RightSidebar>
          <MobileView>
            <PullTab onClick={this.expandClick}  active={this.state.isExpanded?1:0} />
          </MobileView>
          <RightToLeft>
            <Post>
              <Header>
                <HeaderIcon>
                  <PostIcon/>
                </HeaderIcon>
                <PostHeaderTitle>
                  <h1>{post.frontmatter.title}</h1>
                  <PostContentHeader>{post.frontmatter.date}</PostContentHeader>
                </PostHeaderTitle>
              </Header>
              <PostContent dangerouslySetInnerHTML={{ __html: post.html }}/>
              <PostContent>
              <p>
                Regards, <br/><br/>
                ~Hourai Etealune~
              </p>
              </PostContent>
              <Commento/>
            </Post>
          </RightToLeft>
          <Anchor id="bottom-anchor"/>
        </PostFrame>
      </PostWrapper>
      </>
    );
  }
}


export default BlogPost;

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents(pathToSlugField: "fields.path")
      frontmatter {
        title
        url
        date(formatString: "DD MMMM YYYY")
      }
      fields {
        path
      }
    }
  }
`